.home__container {
  padding-left: 78px;
  padding-right: 78px;
  padding-top: 140px;

  h1 {
    font-style: normal;
    font-weight: bold;
    font-size: 100px;
    line-height: 120px;
    /* identical to box height, or 120% */
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: $gradient;
    text-transform: uppercase;
    max-width: 985px;
  }
}

.home__nav-block {
  display: flex;
  justify-content: space-between;
  max-width: 905px;
  width: 100%;
  padding-bottom: 198px;
  padding-top: 127px;
}

.home__main-link {
  font-style: normal;
  font-weight: normal;
  font-size: 21.0909px;
  line-height: 27px;
  /* identical to box height */
  border-bottom: 1.31818px solid rgba(72, 109, 146, 0.2);
  color: rgba(72, 109, 146, 0.6);
}

.home__anchor {
  padding-bottom: 60px;
  padding-left: 50px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: rgba(72, 109, 146, 0.6);;
    transform: rotate(45deg);
    left: 17px;
    top: 12px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: rgba(72, 109, 146, 0.6);;
    left: 26px;
    top: 12px;
    transform: rotate(-45deg);
  }
}

.home__link-anchor {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height */
  color: rgba(72, 109, 146, 0.6);
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: #486D92;
    transform: rotate(45deg);
    left: -36px;
    top: 20px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: #486D92;
    left: -25px;
    top: 20px;
    transform: rotate(-45deg);
  }
}

.header__anchor-arrow {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 2px;
    background-color: rgba(72, 109, 146, 0.3);;
    transform: rotate(45deg);
    left: -31px;
    top: 0;
  }

  &::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 2px;
    background-color: rgba(72, 109, 146, 0.3);;
    left: -23px;
    top: 0;
    transform: rotate(-45deg);
  }
}

.home__item-wrapper {
  width: 100%;
  background: linear-gradient(180deg, #0081C8 0%, #54B6E7 100%);
  //max-width: 678px;
  height: 460px;

  img {
    &:last-child {
      position: relative;
    }

    &:first-child {
      position: relative;
    }
  }

  &--second {
    height: 818px;
    background: linear-gradient(180deg, #4911BE 0%, #2459A9 100%), #C4C4C4;

    img {
      &:first-child {
        position: relative;
        top: 25%;
      }
    }
  }

  &--red {
    background: linear-gradient(180deg, #FC4A4A 0%, #FF4444 100%), #C4C4C4;
  }

  &--orange {
    background-image: url("../images/content/home__item-orange.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.home__projects-list {
  width: 49%;
  display: flex;
  flex-direction: column;
}

.home__projects-container {
  display: flex;
  justify-content: space-between;
}

.home__project-item {
  padding-bottom: 104px;

  h3 {
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 40px;
    /* identical to box height, or 154% */
    text-transform: uppercase;
    padding-top: 26px;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: rgba(72, 109, 146, 0.6);
  }
}

.home__projects-block {
  h2 {
    font-style: normal;
    font-weight: normal;
    font-size: 90px;
    line-height: 100px;
    max-width: 531px;
    padding-bottom: 90px;
  }
}


.home__logos-slider {
  display: flex;
  justify-content: space-between;
  position: relative;
  height: 150px;
  padding: 0;
  margin: 0;
  list-style-type: none;
}


.home__slider {
  padding-bottom: 50px;
  overflow: hidden;
  width: 110%;
}

.home__slider-center {
  padding-bottom: 50px;
  overflow: hidden;
  width: 110%;
}

.home__slider-last {
  overflow: hidden;
  width: 110%;
}

.slider-wrapper {
  position: relative;
  display: flex;
  transition: transform 0.6s ease;
}

.home__slider-wrapper {
  display: flex;
  position: relative;
  transition: transform 1s ease;
}

.home__slider-wrapper-center {
  display: flex;
  position: relative;
  transition: transform 0.6s ease;
}

//.slider__items {
//  display: flex;
//  transition: transform 0.6s ease;
//  width: 100%;
//  margin: 0 -40px;
//}

/*    */
.home__slider-item {
  flex: 0 0 12%;
  max-width: 12%;

  img {
    mix-blend-mode: luminosity;
    fill: black;
    opacity: 0.6;
  }

  svg {
    mix-blend-mode: luminosity;
    fill: black;
  }
}

.home__logos-container {
  h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 50px;
    line-height: 60px;
    /* or 120% */
    text-align: center;
    max-width: 1258px;
    margin: 0 auto;
  }
}

.home__link-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.home__project-link {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 60px;
  /* identical to box height, or 273% */
  padding: 10px 46px;
  margin-top: 60px;
  margin-bottom: 120px;
  border: 1px solid rgba(72, 109, 146, 0.2);
  box-sizing: border-box;
  border-radius: 52px;
  color: var(--main-color);
}

.home__logos {
  padding-bottom: 111px;
}

@media (max-width: $tablet-width) {
  .home__container {
    padding-left: 58px;
    padding-right: 58px;
    padding-top: 91px;

    h1 {
      font-size: 60px;
      line-height: 70px;
    }
  }

  .home__nav-block {
    flex-wrap: wrap;
    padding-top: 42px;
    padding-bottom: 98px;
  }

  .home__main-link {
    margin-bottom: 20px;
  }

  .home__projects-container {
    flex-direction: column;
    align-items: center;
  }

  .home__projects-list {
    width: 100%;
  }

  .home__slider-item {
    flex: 0 0 20%;
    max-width: 20%;
  }

  .home__slider {
    width: 100%;
  }

  .home__slider-center {
    width: 100%;
  }

  .home__slider-last {
    width: 100%;
  }
}

@media (max-width: $mobile-width) {
  .home__slider-item {
    flex: 0 0 25%;
    max-width: 25%;

    img {
      width: 89px;
      height: 36px;
    }
  }

  .home__logos-container {
    h2 {
      font-style: normal;
      font-weight: 500;
      font-size: 22px;
      line-height: 30px;
      /* or 136% */
      padding-left: 20px;
      padding-right: 20px;
      text-align: center;
    }
  }

  .home__project-link {
    //padding: 10px 23px;
    margin-bottom: 40px;
    margin-top: 40px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 50px;
  }

  .home__project-item {
    padding-bottom: 40px;
  }

  .home__container {
    padding-bottom: 50px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .home__projects-block {
    h2 {
      font-style: normal;
      font-weight: normal;
      font-size: 28px;
      line-height: 36px;
      max-width: 218px;
      padding-bottom: 20px;
    }
  }

  .home__logos {
    padding-bottom: 60px;
  }
  
