.slider {
  padding-top: 65px;
  padding-bottom: 60px;
  position: relative;

  img {
    position: absolute;
  }
}

.slider__container {
  background: linear-gradient(94.57deg, #FC4A4A 2.15%, #DC26FA 74.8%), #C4C4C4;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 138px;
}

.slider__list-indicators {
  display: flex;
  justify-content: space-between;
  max-width: 910px;
  width: 100%;
  padding-top: 221px;
  margin: 0 auto;
}

.slider__indicator {
  max-width: 210px;
  width: 25%;
  height: 5px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2.5px;
}

//    li
.slider__indicator-active {
  background: linear-gradient(to right, white 30%, rgba(255, 255, 255, 0.2) 30%);
}

.slider__list-items {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1258px;
  margin: 0 auto;
  padding-top: 63px;
  padding-bottom: 96px;

  li {
    font-style: normal;
    font-weight: 500;
    font-size: 50px;
    line-height: 60px;
    /* or 120% */

    text-align: center;

    color: #FFFFFF;
    max-width: 1258px;
    width: 100%;
  }
}

.slider__link {
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 60px;
  color: white;
  padding: 15px 46px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-sizing: border-box;
  border-radius: 100px;
  margin: 0 auto;
  display: flex;
  max-width: 562px;
  justify-content: center;
}

//c  

.slider__battary {
  position: absolute;
  top: -30px;
  left: 10%;
}

.slider__crystal {
  position: absolute;
  top: 26%;
  left: 78%;
}

.slider__basket {
  position: absolute;
  top: 82%;
  left: 80%;
  padding-right: 20px;
}

.slider__crystal-second {
  position: absolute;
  top: 76%;
  left: 8%;
}

@media (max-width: $tablet-width) {
  .slider__basket {
    left: 70%;
  }

  .slider__list-indicators {
    padding-right: 10px;
    padding-left: 10px;
  }

  .slider__list-items {
    li {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}

@media (max-width: $mobile-width) {
  .slider {
    display: none;
  }
}