html {
  font-family: "EuclidCircularB", "Arial", serif;
  font-size: 16px;
  height: 100%;
}

html[data-theme = "light"] {
  --main-color: #333333;
  --bg-color: #ffffff;
  --footer-bg: #000000;
  --main--footer: #ffffff;
  --footer--opac: rgba(255, 255, 255, 0.4);
  --form-info: rgba(39, 39, 39, 0.4);
  --blog-bg: linear-gradient(to top,#ebedf0 90%,#fff 10%);
  --bg-recomendation: #f2f2f2;
  --color-title: rgba(0, 0, 0, 0.4);
}

html[data-theme = "dark"] {
  --main-color: #ffffff;
  --bg-color: #333333;
  --footer-bg: #ffffff;
  --main--footer: #000000;
  --footer--opac: rgba(0, 0, 0, 0.4);
  --form-info: #ffffff;
  --blog-bg: linear-gradient(to top,#333 100%,#333 100%);
  --bg-recomendation: #282727;
  --color-title: rgba(255, 255, 255, 0.4);
}

body {
  height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background-color: var(--bg-color);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
  color: var(--main-color);
}

ol,
ul,
a {
  list-style: none;
}

a {
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
}

a:visited {
  text-decoration: none;
}

p {
  color: var(--main-color);
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
  color: var(--main-color);
}

input::-ms-clear {
  display: none;
}

input:invalid {
  box-shadow: none;
}

button,
input {
  border: none;
}

audio,
canvas,
iframe,
img,
svg,
video,
textarea {
  vertical-align: middle;
}

textarea {
  resize: none;
}

section {
  position: relative;
}

input,
select,
button {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  &:hover {
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(to right, red, fuchsia);
  }
}
