.home-service {
  padding-left: 78px;
  padding-right: 78px;
  padding-bottom: 65px;
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    font-style: normal;
    font-weight: normal;
    font-size: 90px;
    line-height: 100px;
    /* identical to box height, or 111% */
    padding-bottom: 80px;
    width: 100%;
    color: var(--main-color);
  }
}

.home-service__link-wrapper {
  width: 80px;
  height: 80px;
  background-color: #333333;
  position: absolute;
  z-index: 200;
  border-radius: 50%;
  top: 245px;
  left: 275px;

  &::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: white;
    transform: rotate(-6deg);
    right: 34px;
    top: 30px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: white;
    transform: rotate(97deg);
    right: 28px;
    top: 35px;
  }
}

.home-service__link {
  position: relative;
  z-index: 200;

  &::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: white;
    transform: rotate(-43deg);
    right: -47px;
    top: 36px;
  }
}

.home-service__item {
  width: 361px;
  height: 361px;
  border-radius: 50%;
  background-color: #F7F7F7;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 60px;

  img {
    object-fit: cover;
    z-index: 1;
    position: relative;
    width: 100%;
    height: 100%;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 40px;
    /* or 154% */
    text-align: center;
    color: #FFFFFF;
    max-width: 149px;
    position: absolute;
    top: 143px;
    z-index: 10;
  }
}

.home-service__list {
  width: 100%;
  max-width: 1261px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.home-service__link-style {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 22px;
  border: 1px solid rgba(72, 109, 146, 0.2);
  border-radius: 52px;
  padding: 10px 46px;
  box-sizing: border-box;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right, red, fuchsia);

  &:hover {
    background: linear-gradient(94.57deg, #FC4A4A 2.15%, #DC26FA 74.8%);
    color: #ffffff;
  }
}

.home-service__project-link {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 60px;
  /* identical to box height, or 273% */
  padding: 10px 46px;
  border: 1px solid rgba(72, 109, 146, 0.2);
  box-sizing: border-box;
  border-radius: 52px;
  color: var(--main-color);
  margin-right: 89px;
}

.home-service__link-block {
  padding-top: 20px;
  display: flex;
}

@media (max-width: $tablet-width) {
  .home-service {
    padding-right: 58px;
    padding-left: 58px;

    h2 {
      text-align: center;
    }
  }

  .home-service__link-block {
    flex-direction: column;
  }

  .home-service__project-link {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .home-service__link-style {
    text-align: center;
    display: flex;
    justify-content: center;
    padding: 25px 20px;
  }

  .home-service__list {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
  }

  //.home-service__item {
  //  height: 459px;
  //  max-width: 459px;
  //  //width: 50%;
  //  //flex: 0 0 50%;
  //}
}

@media (max-width: $mobile-width) {
  .home-service__item {
    height: 159px;
    max-width: 159px;
    width: 50%;
    flex: 0 0 50%;


    p {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 40px;
      top: 40px;
    }
  }

  .home-service__big-item {
    max-width: 361px;
    flex: 0 0 100%;
    width: 100%;
    height: 361px;
    margin: 0 auto;
    display: flex;
    flex-basis: 361px;
    margin-bottom: 30px;

    p {
      font-style: normal;
      font-weight: normal;
      font-size: 26px;
      line-height: 40px;
      /* or 154% */
      top: 143px;
      text-align: center;
    }
  }

  .home-service__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    max-width: 375px;
  }

  .home-service {
    padding-right: 20px;
    padding-left: 20px;
    align-items: center;

    h2 {
      display: none;
    }
  }

  .home-service__link-wrapper {
    display: none;
  }

  .home-service__project-link {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 50px;
    padding: 10px 0;
    text-align: center;
  }

  .home-service__link-block {
    width: 100%;
    max-width: 331px;
    justify-content: center;
  }
}