.about__container {
  padding-right: 78px;
  padding-left: 78px;
  padding-bottom: 112px;

  h1 {
    font-style: normal;
    font-weight: bold;
    font-size: 100px;
    line-height: 120px;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: $gradient;
    text-transform: uppercase;
    max-width: 1093px;
    padding-top: 75px;
  }
}

.about__slider-list {
  display: flex;
  position: relative;
  justify-content: space-between;
}

.about__slider-item {
  flex: 0 0 42%;
  max-width: 573px;
}

.about__slider {
  overflow: hidden;
  padding-bottom: 40px;
  width: 110%;
}

.about__sliders {
  display: flex;
  flex-direction: column;
  padding-bottom: 95px;
  width: 100%;
}

.about__team-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.about__img-wrap {
  width: 362px;
  height: 362px;
  background: #F6F6F6;
  border-radius: 50%;
}