.home-media {
  padding-left: 78px;
  padding-right: 78px;
  padding-bottom: 102px;
  padding-top: 60px;

  h2 {
    font-style: normal;
    font-weight: normal;
    font-size: 90px;
    line-height: 100px;
    /* or 111% */
    max-width: 444px;
    color: var(--main-color);
    padding-bottom: 120px;
    width: 100%;
  }
}

.home-media__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-media__list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-bottom: 90px;
  transition: transform 1.2s ease;
}

.home-media__item {
  flex: 0 0 33.333333%;
  max-width: 33.33333%;

  h3 {
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 50px;
    /* or 139% */
    color: var(--main-color);
    max-width: 356px;
    width: 100%;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    text-transform: lowercase;
    color: rgba(72, 109, 146, 0.6);
  }
}

.home-media__project-link {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 60px;
  /* identical to box height, or 273% */
  padding: 10px 40px;
  border: 1px solid rgba(72, 109, 146, 0.2);
  box-sizing: border-box;
  border-radius: 52px;
  color: var(--main-color);
}


@media (max-width: 1220px) {
  .home-media__list {
    flex-direction: row;
    justify-content: space-between;
    margin: 0 -15px;
  }

  .home-media__item {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (max-width: $tablet-width) {
  .home-media {
    padding-right: 58px;
    padding-left: 58px;
  }

  .home-media__item {
    padding-left: 10px;
    padding-right: 10px;

    h3 {
      font-style: normal;
      font-weight: normal;
      font-size: 26px;
      line-height: 36px;
    }
  }
}

@media (max-width: $mobile-width) {
  .home-media__item {
    flex: 0 0 80%;
    max-width: 80%;
    padding-right: 20px;
    padding-left: 20px;
  }

  .home-media {
    padding-left: 0;
    padding-right: 0;
    overflow: hidden;

    h2 {
      font-style: normal;
      font-weight: normal;
      font-size: 28px;
      line-height: 36px;
      padding-bottom: 60px;
      padding-left: 20px;
      max-width: 159px;
    }
  }

  .home-media__project-link {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 50px;
  }
}